
import React from 'react';
import { Box, Button, Flex, Heading, Text, Link, Image } from 'rebass'
import './App.css';
import insta from './insta.png';
import fb from './fb.png';


class App extends React.Component{
  constructor() {
    super();
    
    this.state = {

      privacyPolicy : {
        title: "Privacy Policy", 
        id: 0,
        data: [
          {header: 'Introduction', body: 'Traincor is public. Exercises, public workouts, and the media they contain are immediately searchable and viewable by anyone with the app. You can also compile private workouts that are only available to you.' +
          'When you use Traincor, even if you are not logged into an account, we receive some information from you like the type of device you’re using. You can choose to share additional information with us like your email address and the information you put into your public profile. We use this information to keep your account secure.' +
          'In addition, we use your posted workouts and exercises, in-app search history, and in-app viewing history to show you more relevant content.'
          },
          {header: 'Information You Share', body: 'You don’t have to create an account to use some of our features, such as searching and viewing public workouts and exercises. We still receive information in this circumstance, even if you have not created an account. For example, we collect data about what keywords you search for and what workouts you view to determine what content is popular and what content you may want to view in the future.' +
          'If you do choose to create an account, you must provide us with an email address, password, and display name. Your display name is always public, but your email address and password remain private. You are responsible for the media and information you provide on Traincor.'
          },
          {header: 'Information We Share', body: 'We do not share your personal data without your consent. However, popular trends in user data, such as what workouts are popular, may be shared with potential advertising partners.' +
          'We engage service providers to perform functions and provide services for us in the United States and other countries. For example, we use a variety of third-party services to help operate our services, such as to host our databases and help us understand our users. We may share your private data with such service providers subject to obligations consistent with this Policy and any other appropriate confidentiality and security measures. These services must use your personal data only on our behalf and according to our instructions.'
          },
          {header: 'Information We Repost', body: 'Users retain ownership of the content they post.  However, we reserve the right to repost the content associated with exercises and public workouts on other social media platforms, such as Instagram and Facebook.'},
          {header: 'Law and Harm', body: 'Nothing in this Privacy Policy is intended to limit any legal defenses or objections that you may have to a third party’s request to disclose your personal data. Notwithstanding anything to the contrary in this Privacy Policy, we may preserve, use, share, or disclose your data if we believe that it is reasonably necessary to do any of the following:' +
          'to comply with a law, regulation, legal process, or governmental request; to protect the safety of any person; to protect the safety or integrity of our platform or to explain why we have removed content; to address fraud, security, or technical issues; or to protect our rights and property or the rights and property of those who use our services.'
          },
          {header: 'Account Deletion', body: 'We do not currently keep backup account data for account deletions. Therefore, if you delete your account, all your posted workouts, exercises, and other data will be permanently deleted and no longer viewable by yourself or other users.'},
          {header: 'Children', body: 'Traincor is not to be used by children under the age of 13. Users must also be old enough to consent to the processing of their personal data in their country.'},
        ]},
      termsOfUse : {
        title: "Terms of Use Policy", 
        id: 1,
        data: [
          {header: '', body: 'If your content or profile violates the following Terms of Use in any way, Traincor reserves the right to modify the content or remove it completely if neccessary.'},
          {header: '', body: 'You may not impersonate or make an account for another person without their express permission.'},
          {header: '', body: 'You may not use Traincor as a platform for hate speech or to induce violence.'},
          {header: '', body: 'You may not reproduce content uploaded by other users for monetary gain.'},
          {header: '', body: 'You may not post private or confidential information belonging to someone else or violate the rights of another person in any way.'},
          {header: '', body: 'All uploaded media must conform to the Media Upload Guidelines.'},
        ]},
        mediaPolicy : {
          title: "Media Upload Policy", 
          id: 2,
          data: [
            {header: 'All Media', body: 'Media cannot contain nudity, violence, copywritten, or otherwise offensive material.  Media also may not violate the intellectual property of another person. Any media that does not meet these and the following terms may be removed.'},
            {header: 'Exercise Uploads', body: 'Exercises may be removed if they include vulgar or confusing language or contain inaccurate or misleading information regarding the muscles and equipment used.'},
        ]},
        liabilityPolicy: {
          title: "Liability Policy", 
          id: 3,
          data: [
            {header: '', body: 'Traincor does not act as a medical entity.  In addition, do not assume any Traincor users are trained medical professionals and do not take advice found on our platform as that of a medical professional.'},
            {header: '', body: 'Exercises and workouts on Traincor are provided solely for informational purposes.  Our content is not intended to treat, cure, or prevent any disease and should not be taken as medical advice.'},
            {header: '', body: 'Exercising involves taking risks, so you should consult your doctor before starting any new exercise program.  In addition, stop exercising immediately and consult a medical professional if you feel pain when exercising.'},
            {header: '', body: 'You acknowledge that you take full responsibility for your own health.  Traincor expressly disclaims responsibility for any liability, injury, or damage caused by the use or interpretation of any content found on our platform.'},
        ]},
      expandedRows : []
    };
  }

  handleRowClick(rowId) {
    const currentExpandedRows = this.state.expandedRows;
    const isRowCurrentlyExpanded = currentExpandedRows.includes(rowId);
    
    const newExpandedRows = isRowCurrentlyExpanded ? 
    currentExpandedRows.filter(id => id !== rowId) : 
    currentExpandedRows.concat(rowId);
    
    this.setState({expandedRows : newExpandedRows});
  }

  renderSection(header, body) {
    return (
      <Box>
        <Text sx={{my: 20, fontWeight: 'bold', mr: 20, ml: 20}} fontSize={3}>{header}</Text>
        <Text sx={{my: 20, textAlign: 'start', mr: 20, ml: 20 }}>{body}</Text>
      </Box>
    );
  }

  renderItem(item) {
    let allSections = [];
      
    item.data.forEach(section => {
      const perItemRows = this.renderSection(section.header, section.body);
      allSections = allSections.concat(perItemRows);
    });

    const clickCallback = () => this.handleRowClick(item.id);
    const itemRows = [
      <Button onClick={clickCallback} key={"row-data-" + item.id}>
        <Text sx={{ padding: 10, fontWeight: 'bold', alignSelf: 'center', color: 'black' }} fontSize={3}>{item.title}</Text>		
      </Button>
    ];
      
    if(this.state.expandedRows.includes(item.id)) {
      itemRows.push(
        <tr key={"row-expanded-" + item.id}>
          {allSections}
        </tr>
      );
    }
      
    return itemRows;    
  }

  render() {

    let pp = this.renderItem(this.state.privacyPolicy)
    let tp = this.renderItem(this.state.termsOfUse)
    let lp = this.renderItem(this.state.liabilityPolicy)
    let mp = this.renderItem(this.state.mediaPolicy)
    
    return (
      <div className="App">
        <body className="App-body">
            <Box
              sx={{
                px: 4,
                py: 6,
                backgroundImage: 'url(https://source.unsplash.com/random/1024x768?exercise)',
                backgroundSize: 'cover',
                color: 'white',
                bg: 'gray',
              }}>
              <Heading
                textAlign='center'
                fontSize={[ 7, 8 ]}
                sx={{fontWeight: 'bold'}}
                >
                TRAINCOR
              </Heading>
              <Flex width={3/4} sx={{justifyContent:'flex-end'}}>
              
              <a href="https://www.instagram.com/traincor.io/" target='blank'>
                <Button sx={{width: 80, height: 80 , backgroundColor: '#00000000'}}>
                    <Image
                      src={insta}
                      sx={{
                        borderRadius: 40,
                      }}
                    />
                  </Button>
              </a>
                
  
              <a href="https://www.facebook.com/traincor.io" target='blank'>
                <Button sx={{width: 80, height: 80 , backgroundColor: '#00000000'}}>
                    <Image
                      src={fb}
                      sx={{
                        borderRadius: 40,
                      }}
                    />
                  </Button>
              </a>
              </Flex>
            </Box>
          
          <Text sx={{margin: 70, mb: 50, font: 'Menlo, monospace'}} fontSize={4}>
            Our Mission: compile the world's largest database of exercises and workouts for athletes, trainers, coaches, gym rats, and home workout warriors.
          </Text>

          <Flex width={1} sx={{justifyContent: 'space-around', mb: 50}}>
            <Link sx={{ color: '#004AAD', padding: 10, fontWeight: 'bold' }} fontSize={3}>DOWNLOAD iOS</Link>
            <Link sx={{ color: '#004AAD', padding: 10, fontWeight: 'bold' }} fontSize={3}>DOWNLOAD ANDROID</Link>
          </Flex>

          {pp}
          {lp}
          {tp}
          {mp}

          <Box sx={{height: 50}}/>

        </body>
      </div>
    );
  }
}

export default App;